div.view-PageHeader {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    margin: 0;
    padding: 0;
    overflow: hidden;
}

div.view-PageHeader > header {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    height: 2rem;
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 2rem;
    margin: 0 0 1rem 0;
    width: 100%;
    flex: 0 0 auto;
}

div.view-PageHeader > header > div {
    flex: 0 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 1rem;
}

div.view-PageHeader > header > div > div.label {
    font-variant: small-caps;
    font-weight: 900;
}

div.view-PageHeader > section {
    position: absolute;
    left: 0;
    top: 2rem;
    right: 0;
    bottom: 0;
    padding: .5rem;
}
